import { css } from 'glamor'

const style = {
  row: css({
    display: 'flex',
    flexDirection: 'row',
    listStyle: 'none',
    margin: 0,
    padding: '6%',
    width: '100%',
    zIndex: 5,
  }),
  col: css({
    display: 'block',
    padding: '0 2rem',
    position: 'relative',
  }),
  col1: css({
    width: '20%',
  }),
  col2: css({
    width: '40%',
  }),
  col3: css({
    width: '60%',
  }),
  col4: css({
    width: '80%',
  }),
  push1: css({
    marginLeft: '20%',
  }),
  push2: css({
    marginLeft: '40%',
  }),
  push3: css({
    marginLeft: '60%',
  }),
  push4: css({
    marginLeft: '80%',
  }),
  alignRight: css({
    textAlign: 'right',
  }),
}

export default style



