import { css } from 'glamor'
import imgSpace from '../img/bg-space.png'

const style = {
  bgEta: css({
    backgroundImage: `url(${imgSpace})`,
    backgroundSize: 'cover',
    bottom: 0,
    left: 0,
    position: 'fixed',
    top: 0,
    width: '50%',
    zIndex: '1',
  }),
}

export default style



