import { css } from 'glamor'

const style = {
  link: css({
    color: 'black',
    fontSize: '1.3rem',
    letterSpacing: '0.05rem',
    textDecoration: 'none',
    textTransform: 'uppercase',
  }),
  number: css({
    color: 'black',
    display: 'block',
    fontSize: '6rem',
    fontWeight: 'bold',
    letterSpacing: '-0.06rem',
    margin: 0,
    overflow: 'hidden',
    textIndent: '-2.1rem',
    textTransform: 'uppercase',
  }),
  workType: css({
    fontSize: '2.3rem',
    fontWeight: 'bold',
    left: '-5.3rem',
    letterSpacing: '-0.144',
    lineHeight: '2.6rem',
    margin: 0,
    position: 'absolute',
    textTransform: 'uppercase',
    top: '17rem',
    transform: 'rotate(90deg)',
    width: '19rem',
  }),
  workTitle: css({
    fontFamily: 'Playfair Display',
    fontSize: '14.4rem',
    fontWeight: '900',
    letterSpacing: '-1.144',
    margin: 0,
    textTransform: 'uppercase',
  }),
}

export default style



